.control-bar {
  position: fixed;
  z-index: 999;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 64px;
  border-top: 1px solid #e6e6e6;
  background-color: #ffffff;
  -webkit-transition: all .2s;
  -o-transition: all .2s;
  transition: all .2s;
  -webkit-transform: translateY(100%);
  -ms-transform: translateY(100%);
  transform: translateY(100%); }
  .control-bar.js-active {
    -webkit-transform: none;
    -ms-transform: none;
    transform: none; }



