@font-face {
  font-family: "Montserrat Alternates";
  font-style: normal;
  font-weight: normal;
  src: url("assets/fonts/montserrat-alternates/regular.ttf") format("truetype"); }

@font-face {
  font-family: "Montserrat Alternates";
  font-style: normal;
  font-weight: bold;
  src: url("assets/fonts/montserrat-alternates/bold.ttf") format("truetype"); }

@font-face {
  font-family: "Montserrat Alternates";
  font-style: italic;
  font-weight: normal;
  src: url("assets/fonts/montserrat-alternates/italic.ttf") format("truetype"); }

@font-face {
  font-family: "Montserrat Alternates";
  font-style: italic;
  font-weight: bold;
  src: url("assets/fonts/montserrat-alternates/bolditalic.ttf") format("truetype"); }

.pagination-btn {
  outline: none;
  border-radius: 50%;
  border: none;
  background-color: #ffffff;
  color: #464647;
  min-width: 30px;
  height: 30px;
  box-shadow: 0 2px 10px -2px rgba(0, 0, 0, 0.2);
  cursor: pointer; }
  .pagination-btn:not(.pagination-btn_prev):not(.pagination-btn_next) {
    transition: background .2s; }
  .pagination-btn.js-current-page, .pagination-btn:hover {
    background-color: #464647;
    color: #ffffff; }

body, html {
  font-family: "Montserrat Alternates", sans-serif;
  font-size: 16px;
  line-height: normal;
  font-weight: 400;
  color: #4a4a4a;
  background: #fafafa;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  padding-bottom: 300px; }

.wrapper {
  width: 1230px;
  margin-left: auto;
  margin-right: auto;
  max-width: 100%; }
  @media screen and (max-width: 1279px) {
    .wrapper {
      width: 980px; } }
  @media screen and (max-width: 1023px) {
    .wrapper {
      width: 660px; } }

.app {
  padding-top: 50px; }
  .app .el-counter {
    margin-bottom: 15px; }

button {
  -webkit-transition: all .2s;
  -o-transition: all .2s;
  transition: all .2s; }
  button:active {
    -webkit-transform: scale(0.9);
    -ms-transform: scale(0.9);
    transform: scale(0.9); }



